<template>
  <div class="ship">
    <div class="table_con">
      <div style="display: flex; margin-left: 20px; margin-top: 20px">
        <el-date-picker
          v-model="date1"
          type="daterange"
          placeholder="选择日期"
          value-format="yyyy-MM-dd"
          @change="get_data(date1)"
        >
        </el-date-picker>
        <!-- <a class="down_btn" :href="a_href" @click="down_excel"
                    >一键导出</a
                > -->
        <el-button type="success" @click="down_excel">一键导出</el-button>
      </div>

      <el-table
        max-height="750"
        :data="tableData"
        style="width: 1290px; margin: auto; margin-top: 20px"
        border
        v-loading="loading"
        element-loading-text="拼命加载中,请稍等"
      >
        <el-table-column prop="order_id" label="亚马逊订单号" width="200">
        </el-table-column>
        <el-table-column prop="fake_tracking_no" label="假回传单号" width="200">
        </el-table-column>
        <el-table-column prop="country" label="国家" width="50">
        </el-table-column>
        <el-table-column prop="station" label="站点" width="50">
        </el-table-column>
        <el-table-column prop="dates" label="时间" width="150">
        </el-table-column>
        <el-table-column prop="status" label="是否覆盖" width="170">
          <template slot="header" slot-scope="{}">
            <el-select @change="change" v-model="statusVal">
              <el-option value="全部" label="是否覆盖(全部)"></el-option>
              <el-option value="0" label="是否覆盖(否)"></el-option>
              <el-option value="1" label="是否覆盖(是)"></el-option>
            </el-select>
          </template>
        </el-table-column>
        <el-table-column prop="remark" label="备注" width="450">
          <template slot-scope="scope">
            <!-- <input
                            type="number"
                            v-model="scope.row[4]"
                            @change="editChange(scope.$index)"
                        /> -->
            <el-input
              v-model="scope.row['remark']"
              @change="editChange(scope.row['remark'], scope.row['id'])"
              placeholder="请输入备注"
            ></el-input>
          </template>
        </el-table-column>
      </el-table>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      statusVal: "全部", //状态值
      tableData: [
        {
          order_id: "",
          fake_tracking_no: "",
          dates: "",
          id: "",
          status: "",
          country: "",
          station: "",
          remark: ""
        }
      ],
      loading: false,
      date1: "",
      a_href: "#",
      tHeader: [
        {
          title: "亚马逊订单号",
          key: ""
        },
        {
          title: "假回传单号",
          key: ""
        },
        {
          title: "国家",
          key: ""
        },
        {
          title: "站点",
          key: ""
        },
        {
          title: "时间",
          key: ""
        },
        {
          title: "备注",
          key: ""
        }
      ]
    };
  },
  props: ["type"],
  mounted() {
    this.get_data("");
  },
  methods: {
    // 筛选状态
    change() {
      console.log("选择状态", this.statusVal);
      this.get_data(this.date1);
    },
    //获取表格数据
    get_data(dates) {
      console.log("1111");
      let url = "server/order_management/fake_tracking_no_view/";
      let that = this;
      this.loading = true;
      let formdata = new FormData();
      formdata.append("dates", dates);
      formdata.append("status", this.statusVal);
      this.$axios.post(this._url2 + url, formdata).then(res => {
        console.log(res.data);
        if (res.data.code === 200) {
          that.tableData = res.data.data;
          that.loading = false;
          that.tableData.forEach((item, index) => {
            if (item.status == "0") {
              item.status = "否";
            } else {
              item.status = "是";
            }
          });
        } else {
          alert(res.data.msg);
        }
      });
    },
    //下载excel表格
    down_excel() {
      (this.tHeader = [
        {
          title: "亚马逊订单号",
          key: "order_id"
        },
        {
          title: "假回传单号",
          key: "fake_tracking_no"
        },
        {
          title: "国家",
          key: "country"
        },
        {
          title: "站点",
          key: "station"
        },
        {
          title: "时间",
          key: "dates"
        },
        {
          title: "备注",
          key: "remark"
        }
      ]),
        this.$utils.export2Excel(
          this.tHeader,
          this.tableData,
          "假单号回传数据"
        );
    },
    //编辑备注
    editChange(e, id) {
      console.log(e, id);
      let url = "server/order_management/edit_remark/";
      let that = this;
      let formdata = new FormData();
      formdata.append("id", id);
      formdata.append("remark", e);
      this.$axios.post(this._url2 + url, formdata).then(res => {
        console.log(res.data);
        if (res.data.code === 200) {
          console.log("备注成功");
        } else {
          alert("备注失败");
        }
      });
    }
  }
};
</script>

<style scoped>
.down_btn {
  display: inline-block;
  width: 100px;
  height: 40px;
  border: 1px solid #67c23a;
  border-radius: 5px;
  text-align: center;
  line-height: 40px;
  background: #67c23a;
  color: #fff;
  margin-left: 10px;
}
</style>
<style scoped>
.ship > .shipOption {
  font-size: 16px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.ship > .shipOption > div {
  background: rgba(255, 255, 255, 0.9);
  height: 60px;
  line-height: 60px;
  padding: 0 30px;
  margin: 0 30px 20px;
}
.ship > .shipOption input[type="file"] {
  width: 170px;
}
.div_column {
  display: flex;
  justify-content: space-around;
  width: 60%;
  margin: auto;
}
</style>
<style>
.el-textarea .el-textarea__inner {
  resize: none;
}
.ship .el-table .el-input__inner {
  border: none;
}
</style>



